'use client'

import { useState } from 'react'
import Link from 'next/link'
import { ManualAction, ManualActionType, ManualActionStatus } from '@/lib/conversations/types'
import { mockManualActions } from '@/lib/conversations/mock-data'

// ==========================================
// RISIVO CRM - Manual Actions Page
// Tasks that require manual intervention
// ==========================================

// Tab Configuration - Shared across Conversations module
const CONVERSATION_TABS = [
  { id: 'conversations', label: 'Conversations', href: '/sub-account/conversations' },
  { id: 'power-dialer', label: 'Power Dialer', href: '/sub-account/conversations/power-dialer' },
  { id: 'manual-actions', label: 'Manual Actions', href: '/sub-account/conversations/manual-actions' },
  { id: 'message-templates', label: 'Message Templates', href: '/sub-account/conversations/message-templates' },
  { id: 'smart-links', label: 'Smart Links', href: '/sub-account/conversations/smart-links' },
]

// Action Type Icons
const actionTypeIcons: Record<ManualActionType, { icon: string; color: string }> = {
  call: { icon: 'ðŸ“ž', color: 'bg-blue-100 text-blue-700' },
  sms: { icon: 'ðŸ’¬', color: 'bg-green-100 text-green-700' },
  email: { icon: 'ðŸ“§', color: 'bg-purple-100 text-purple-700' },
  whatsapp: { icon: 'ðŸŸ¢', color: 'bg-emerald-100 text-emerald-700' },
  task: { icon: 'âœ…', color: 'bg-gray-100 text-gray-700' },
}

// Status Colors
const statusColors: Record<ManualActionStatus, string> = {
  pending: 'bg-yellow-100 text-yellow-700',
  in_progress: 'bg-blue-100 text-blue-700',
  completed: 'bg-green-100 text-green-700',
  skipped: 'bg-gray-100 text-gray-700',
  failed: 'bg-red-100 text-red-700',
}

export default function ManualActionsPage() {
  const [actions, setActions] = useState<ManualAction[]>(mockManualActions)
  const [filterType, setFilterType] = useState<'all' | ManualActionType>('all')
  const [filterWorkflow, setFilterWorkflow] = useState<string>('all')
  const [filterAssignee, setFilterAssignee] = useState<string>('all')
  const [filterStatus, setFilterStatus] = useState<'all' | ManualActionStatus>('all')
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedActions, setSelectedActions] = useState<string[]>([])

  // Get unique workflows from actions
  const workflows = Array.from(new Set(actions.map(a => a.workflowName).filter(Boolean)))
  
  // Get unique assignees
  const assignees = Array.from(new Set(actions.map(a => a.assignedToName).filter(Boolean)))

  // Filter actions
  const filteredActions = actions.filter(action => {
    if (filterType !== 'all' && action.type !== filterType) return false
    if (filterWorkflow !== 'all' && action.workflowName !== filterWorkflow) return false
    if (filterAssignee !== 'all' && action.assignedToName !== filterAssignee) return false
    if (filterStatus !== 'all' && action.status !== filterStatus) return false
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      if (!action.contactName.toLowerCase().includes(query) && 
          !action.contactEmail?.toLowerCase().includes(query) &&
          !action.contactPhone?.toLowerCase().includes(query)) {
        return false
      }
    }
    return true
  })

  // Format date
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
      hour: 'numeric',
      minute: '2-digit',
    })
  }

  // Handle select all
  const handleSelectAll = () => {
    if (selectedActions.length === filteredActions.length) {
      setSelectedActions([])
    } else {
      setSelectedActions(filteredActions.map(a => a.id))
    }
  }

  // Handle action completion
  const handleComplete = (actionId: string) => {
    setActions(prev => prev.map(a => 
      a.id === actionId 
        ? { ...a, status: 'completed' as ManualActionStatus, completedAt: new Date().toISOString() }
        : a
    ))
  }

  // Handle action skip
  const handleSkip = (actionId: string) => {
    setActions(prev => prev.map(a => 
      a.id === actionId 
        ? { ...a, status: 'skipped' as ManualActionStatus }
        : a
    ))
  }

  // Check if there are pending actions
  const hasPendingActions = filteredActions.some(a => a.status === 'pending' || a.status === 'in_progress')

  return (
    <div className="h-[calc(100vh-8rem)] flex flex-col">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-gray-200 px-6 py-4">
        <div className="flex items-center justify-between mb-4">
          <h1 className="text-2xl font-bold text-gray-900">Conversations</h1>
        </div>
        
        {/* Tab Navigation */}
        <div className="flex items-center gap-6 border-b border-gray-200 -mb-4">
          {CONVERSATION_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`pb-3 px-1 text-sm font-medium border-b-2 transition-colors ${
                tab.id === 'manual-actions'
                  ? 'border-blue-600 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-auto bg-gray-50 p-6">
        <div className="bg-white rounded-lg shadow-sm border border-gray-200">
          {/* Header */}
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center justify-between mb-4">
              <div>
                <h2 className="text-xl font-bold text-gray-900">Manual Actions</h2>
                <p className="text-sm text-gray-500 mt-1">
                  Manual Actions are tasks that require you to manually place calls or send SMS messages to contacts.
                </p>
              </div>
              {hasPendingActions && (
                <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 font-medium flex items-center gap-2">
                  <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  Let's Start
                </button>
              )}
            </div>

            {/* Filters */}
            <div className="flex items-center gap-4 flex-wrap">
              {/* Type Filter */}
              <div className="flex items-center gap-2">
                <select
                  value={filterType}
                  onChange={(e) => setFilterType(e.target.value as any)}
                  className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Types</option>
                  <option value="call">ðŸ“ž Calls</option>
                  <option value="sms">ðŸ’¬ SMS</option>
                  <option value="email">ðŸ“§ Email</option>
                  <option value="whatsapp">ðŸŸ¢ WhatsApp</option>
                  <option value="task">âœ… Tasks</option>
                </select>
              </div>

              {/* Workflow Filter */}
              <div className="flex items-center gap-2">
                <select
                  value={filterWorkflow}
                  onChange={(e) => setFilterWorkflow(e.target.value)}
                  className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">Select Workflow</option>
                  {workflows.map(workflow => (
                    <option key={workflow} value={workflow}>{workflow}</option>
                  ))}
                </select>
              </div>

              {/* Assignee Filter */}
              <div className="flex items-center gap-2">
                <select
                  value={filterAssignee}
                  onChange={(e) => setFilterAssignee(e.target.value)}
                  className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">Select Assignee</option>
                  {assignees.map(assignee => (
                    <option key={assignee} value={assignee}>{assignee}</option>
                  ))}
                </select>
              </div>

              {/* Status Filter */}
              <div className="flex items-center gap-2">
                <select
                  value={filterStatus}
                  onChange={(e) => setFilterStatus(e.target.value as any)}
                  className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Status</option>
                  <option value="pending">Pending</option>
                  <option value="in_progress">In Progress</option>
                  <option value="completed">Completed</option>
                  <option value="skipped">Skipped</option>
                  <option value="failed">Failed</option>
                </select>
              </div>

              {/* Search */}
              <div className="relative flex-1 min-w-[200px]">
                <svg className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
                <input
                  type="text"
                  placeholder="Search contacts..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b border-gray-200">
                <tr>
                  <th className="px-6 py-3 text-left">
                    <input
                      type="checkbox"
                      checked={selectedActions.length === filteredActions.length && filteredActions.length > 0}
                      onChange={handleSelectAll}
                      className="rounded border-gray-300"
                    />
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Contacts
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Workflow
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Assigned To
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Type
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Date Added
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredActions.length === 0 ? (
                  <tr>
                    <td colSpan={8} className="px-6 py-16 text-center">
                      <div className="flex flex-col items-center">
                        <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mb-4">
                          <svg className="w-8 h-8 text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                          </svg>
                        </div>
                        <h3 className="text-lg font-semibold text-gray-900 mb-1">Good Work!</h3>
                        <p className="text-gray-500">You have no pending tasks</p>
                      </div>
                    </td>
                  </tr>
                ) : (
                  filteredActions.map(action => (
                    <tr key={action.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4">
                        <input
                          type="checkbox"
                          checked={selectedActions.includes(action.id)}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setSelectedActions([...selectedActions, action.id])
                            } else {
                              setSelectedActions(selectedActions.filter(id => id !== action.id))
                            }
                          }}
                          className="rounded border-gray-300"
                        />
                      </td>
                      <td className="px-6 py-4">
                        <div>
                          <p className="font-medium text-gray-900">{action.contactName}</p>
                          <p className="text-sm text-gray-500">
                            {action.contactEmail || action.contactPhone}
                          </p>
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        {action.workflowName || '-'}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        {action.assignedToName || 'Unassigned'}
                      </td>
                      <td className="px-6 py-4">
                        <span className={`inline-flex items-center gap-1 px-2.5 py-1 rounded-full text-xs font-medium ${actionTypeIcons[action.type].color}`}>
                          {actionTypeIcons[action.type].icon}
                          <span className="capitalize">{action.type}</span>
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`inline-flex px-2.5 py-1 rounded-full text-xs font-medium capitalize ${statusColors[action.status]}`}>
                          {action.status.replace('_', ' ')}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-500">
                        {formatDate(action.createdAt)}
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-2">
                          {(action.status === 'pending' || action.status === 'in_progress') && (
                            <>
                              <button
                                onClick={() => handleComplete(action.id)}
                                className="p-1.5 text-green-600 hover:bg-green-50 rounded"
                                title="Mark as completed"
                              >
                                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                                </svg>
                              </button>
                              <button
                                onClick={() => handleSkip(action.id)}
                                className="p-1.5 text-gray-600 hover:bg-gray-100 rounded"
                                title="Skip"
                              >
                                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
                                </svg>
                              </button>
                            </>
                          )}
                          <button className="p-1.5 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded">
                            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
                            </svg>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          {filteredActions.length > 0 && (
            <div className="px-6 py-4 border-t border-gray-200 flex items-center justify-between">
              <p className="text-sm text-gray-500">
                Showing {filteredActions.length} of {actions.length} actions
              </p>
              <div className="flex items-center gap-2">
                <button className="px-3 py-1 border border-gray-300 rounded text-sm text-gray-500 hover:bg-gray-50 disabled:opacity-50" disabled>
                  Previous
                </button>
                <button className="px-3 py-1 border border-gray-300 rounded text-sm text-gray-500 hover:bg-gray-50 disabled:opacity-50" disabled>
                  Next
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
